// Font Path Variable
$icon-font-path: "../../../bootstrap-sass/assets/fonts/bootstrap/" !default; /* default project-specific Bootstrap Glyphicon path */

// Colors

$brand-primary:               $active-blue;
$brand-success:               $validation-green;
$brand-info:                  lighten($brand-primary, 50%);
$brand-warning:               $validation-yellow;
$brand-danger:                $error-red;

$tooltip-bg:			            $dark-gray;

//== Form states and alerts
//
//## Define colors for form feedback states and, by default, alerts.

$state-success-text:          $brand-success;
$state-success-bg:            lighten($brand-success, 50%);
// $state-success-border:     darken(adjust-hue($state-success-bg, -10), 5%)

$state-info-text:             $brand-info;
$state-info-bg:               lighten($brand-info, 50%);
// $state-info-border:        darken(adjust-hue($state-info-bg, -10), 7%)

$state-warning-text:          $brand-warning;
$state-warning-bg:            lighten($brand-warning, 40%);
// $state-warning-border:     darken(adjust-hue($state-warning-bg, -10), 5%)

$state-danger-text:           $brand-danger;
$state-danger-bg:             lighten($brand-danger, 50%);
// $state-danger-border:      darken(adjust-hue($state-danger-bg, -10), 5%)

/* --- END chrBootstrap Variables --- */