// chrLogo - mixins - STATIC & ANIMATED //

/* key variables */
$chrLogo-fixed-polygon-width:         200px;
$chrLogo-fixed-thickness:             10px;
$chrLogo-fixed-inner-long-arrow:      82px;
$chrLogo-fixed-arrow-wing:            37.5px;

/* pre-calculated triangle variables.  Side value, between degrees. */
$chrLogo-60deg-90deg-half-thickness:  2.9px;
$chrLogo-60deg-30deg-half-thickness:  5.8px;
$chrLogo-thickness-30deg-60deg:       11.6px;
$chrLogo-fixed-outer-side:            115.5px;  /* half-polygon-width 30deg 60deg */

$chrLogo-fixed-inner-short-arrow:     $chrLogo-fixed-inner-long-arrow - $chrLogo-60deg-30deg-half-thickness - $chrLogo-thickness-30deg-60deg;



// mixin - static //

%chrLogo {

  > .chrIcon.chrLogo {

    &:not(.no-center-dot)::before {
      content: '';
      @include concentric(percentage($chrLogo-60deg-30deg-half-thickness*2/($chrLogo-fixed-polygon-width+($spacing-base*4.5))));
    }

    > sub {
      position: absolute;
      font-family: Verdana;
      font-weight: normal;
      line-height: 1;
      top: 76%;
      left: 85%;
      margin: 0;
    }

    > :not(sub) {
      @include concentric(percentage($chrLogo-fixed-polygon-width/($chrLogo-fixed-polygon-width+($spacing-base*4.5))));
      overflow: visible;

      > * {
        position: absolute;

        &::before, &::after {
          @include pseudo;
        }

        &::before, > :nth-child(2)::before {
          transform: rotate(-30deg);
        }

        &::after, > :nth-child(2)::after {
          transform: rotate(30deg);
        }

        &::before {
          right: 0%;
        }

        &::after {
          left: 0%;
        }

      }

    }

    /* Clock-wise UI-design architecture */

    /* degrees */

    .a1 {
      transform: rotate(30deg);
    }
    .a2 {
      transform: rotate(60deg);
    }
    .a3 {
      transform: rotate(90deg);
    }
    .a4 {
      transform: rotate(120deg);
    }
    .a5 {
      transform: rotate(150deg);
    }
    .a6 {
      transform: rotate(180deg);
    }
    .a7 {
      transform: rotate(210deg);
    }
    .a8 {
      transform: rotate(240deg);
    }
    .a9 {
      transform: rotate(270deg);
    }
    .a10 {
      transform: rotate(300deg);
    }
    .a11 {
      transform: rotate(330deg);
    }
    .a12 {
      transform: rotate(360deg);
    }

    /* hexagon sides */

    .a1, .a3, .a5, .a7, .a9, .a11 {
      left: 50%;
      width: percentage($chrLogo-fixed-outer-side/($chrLogo-fixed-polygon-width+($spacing-base*4.5)));
      margin-left: percentage($chrLogo-fixed-outer-side*(-0.5)/($chrLogo-fixed-polygon-width+($spacing-base*4.5)));
      border-radius: 0;

      &, > * {
        overflow: hidden;
      }

      > * {
        top: 0%;
        right: 0%;
        width: 100%;
        height: percentage($chrLogo-fixed-thickness/$chrLogo-fixed-polygon-width);

        &::before, &::after {
          bottom: 0%;
          width: 100%;
          height: 490%;
        }

      }

    }

    /* arrows */

    .a2, .a4, .a6, .a8, .a10, .a12 {

      > * {
        bottom: 50%;
        left: 50%;
        width: percentage($chrLogo-fixed-thickness/$chrLogo-fixed-polygon-width);
        margin-left: percentage($chrLogo-fixed-thickness/$chrLogo-fixed-polygon-width)*(-0.5);

        &::before, &::after {
          width: percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
        }

        > * {
          position: absolute;
          left: 0%;
          width: 100%;
        }

        > :nth-child(1) {
          bottom: 0;
          height: 100%;
        }

        > :nth-child(2) {

          &::before, &::after {
            @include pseudo;
            width: 58%;
            height: 100%;
          }

        }

      }

    }

    /* long arrows */

    .a2, .a6, .a10 {

      > * {
        height: percentage($chrLogo-fixed-inner-long-arrow/$chrLogo-fixed-polygon-width);
        margin-bottom: percentage(($chrLogo-60deg-90deg-half-thickness)/$chrLogo-fixed-polygon-width);

        &::before, &::after {
          top: 7.1%;
          height: percentage($chrLogo-fixed-thickness/$chrLogo-fixed-inner-long-arrow);
        }

      }

    }

    /* short arrows */

    .a4, .a8, .a12 {

      > * {
        height: percentage($chrLogo-fixed-inner-short-arrow/$chrLogo-fixed-polygon-width);
        margin-bottom: percentage(($chrLogo-60deg-30deg-half-thickness + $chrLogo-thickness-30deg-60deg + $chrLogo-60deg-90deg-half-thickness)/$chrLogo-fixed-polygon-width);

        &::before, &::after {
          top: 9%;
          height: percentage($chrLogo-fixed-thickness/$chrLogo-fixed-inner-short-arrow);
        }

        > :nth-child(2) {
          top: 100%;
          height: percentage($chrLogo-fixed-thickness/$chrLogo-fixed-inner-short-arrow);

          &::before, &::after {
            bottom: 78%;
          }

          &::before {
            right: 21%;
          }

          &::after {
            left: 21%;
          }

        }

      }

    }

  }

}

@mixin chrLogo_icon($polygon-width: 245px, $color: $chr-light-blue, $color-opacity: 1, $bg-color: $white, $bg-color-opacity: 1) {

  @extend %chrLogo;

  > .chrIcon.chrLogo {
    @include concentric($polygon-width); /* concentric(percentage(($chrLogo-fixed-polygon-width+($spacing-base*4.5))/$chrLogo-fixed-polygon-width)) */
    background:rgba($bg-color,$bg-color-opacity);

    &:not(.no-center-dot)::before,
    > :not(sub) > ::before, > :not(sub) > ::after,
    > :not(sub) > * > :nth-child(1),
    > :not(sub) > * > :nth-child(2)::before, > :not(sub) > * > :nth-child(2)::after {
      background:rgba($color,$color-opacity);
    }

    > sub {
      font-size:14.5px*$polygon-width/$chrLogo-fixed-polygon-width;
      color:rgba($color,$color-opacity);
    }

  }

}



// mixin - animated //

%chrLogo4D {

  > .chrIcon.chrLogo {
    opacity:0;
    animation-timing-function:ease-out;

    &, > sub,
    .a2 > *, .a6 > *, .a10 > *, .a2 > ::before, .a6 > ::before, .a10 > ::before, .a2 > ::after, .a6 > ::after, .a10 > ::after,
    .a2 > * > *, .a6 > * > *, .a10 > * > *,
    .a4 > *, .a8 > *, .a12 > *, .a4 > ::before, .a8 > ::before, .a12 > ::before, .a4 > ::after, .a8 > ::after, .a12 > ::after,
    .a4 > * > *, .a8 > * > *, .a12 > * > *,
    .a7 > *, .a9 > *, .a11 > *, .a1 > *, .a3 > *, .a5 > *
    { animation-fill-mode: forwards; animation-timing-function:linear; }

    > sub {
      opacity:0;
    }

    /* arrows */

    .a2, .a6, .a10, .a4, .a8, .a12 {

      > * {

        &::before, &::after {
          width:58%;
        }

        &::before {
          right:21%;
        }

        &::after {
          left:21%;
        }

        > :nth-child(1) {
          height:0;
        }

      }

    }

    /* long arrows */

    .a2, .a6, .a10 {

      > * {

        &::before, &::after {
          top:97.4%;
        }

      }

    }

    /* short arrows */

    .a4, .a8, .a12 {

      > * {

        &::before, &::after, > :nth-child(2) {
          opacity:0;
        }

        &::before, &::after {
          top:87.6%;
        }

      }

    }

    /* hexagon sides */
    /* Customized classes available as needed for "pointing" to surrounding text */

    .a1, .a3, .a5, .a7, .a9, .a11 { right:100%; }

  }

}

@mixin chrLogo4D_icon($polygon-width: 245px, $color: $chr-light-blue, $color-opacity: 1, $bg-color: $white, $bg-color-opacity: 1, $loop-duration: 14s, $loop-count:infinite, $loop-delay:0, $animation-instance: 0) {

  @include chrLogo_icon($polygon-width, $color, $color-opacity, $bg-color, $bg-color-opacity);

  @extend %chrLogo4D;

  > .chrIcon.chrLogo { animation-name:fade_#{$animation-instance};

    &, > sub,
    .a2 > *, .a6 > *, .a10 > *, .a2 > ::before, .a6 > ::before, .a10 > ::before, .a2 > ::after, .a6 > ::after, .a10 > ::after,
    .a2 > * > *, .a6 > * > *, .a10 > * > *,
    .a4 > *, .a8 > *, .a12 > *, .a4 > ::before, .a8 > ::before, .a12 > ::before, .a4 > ::after, .a8 > ::after, .a12 > ::after,
    .a4 > * > *, .a8 > * > *, .a12 > * > *,
    .a7 > *, .a9 > *, .a11 > *, .a1 > *, .a3 > *, .a5 > *
    { animation-duration:$loop-duration; animation-iteration-count:$loop-count; animation-delay:$loop-delay; }

    > sub { animation-name:opacify-sub_#{$animation-instance}; }

    /* long arrows */

    .a2, .a6, .a10 {

      > * {

        &::before { animation-name:grow-long-arrow-left-wing_#{$animation-instance}; }

        &::after { animation-name:grow-long-arrow-right-wing_#{$animation-instance}; }

        > :nth-child(1) { animation-name:grow-long-arrows_#{$animation-instance}; }

      }

    }

    /* short arrows */

    .a4, .a8, .a12 {

      > * {

        &::before { animation-name:grow-short-arrow-left-wing_#{$animation-instance}; }

        &::after { animation-name:grow-short-arrow-right-wing_#{$animation-instance}; }

        > :nth-child(1) { animation-name:grow-short-arrows_#{$animation-instance}; }

        > :nth-child(2) { animation-name:begin-short-arrows_#{$animation-instance}; }

      }

    }

    /* hexagon sides */
    /* Customized classes available as needed for "pointing" to surrounding text */

    &.connect-at-hr-1, &.connect-at-hr-2 {

      .a1 > * { animation-name:border2_#{$animation-instance}; }

      .a3 > * { animation-name:border3_#{$animation-instance}; }

      .a5 > * { animation-name:border4_#{$animation-instance}; }

      .a7 > * { animation-name:border5_#{$animation-instance}; }

      .a9 > * { animation-name:border6_#{$animation-instance}; }

      .a11 > * { animation-name:border1_#{$animation-instance}; }

    }

    &.connect-at-hr-3, &.connect-at-hr-4 {

      .a1 > * { animation-name:border1_#{$animation-instance}; }

      .a3 > * { animation-name:border2_#{$animation-instance}; }

      .a5 > * { animation-name:border3_#{$animation-instance}; }

      .a7 > * { animation-name:border4_#{$animation-instance}; }

      .a9 > * { animation-name:border5_#{$animation-instance}; }

      .a11 > * { animation-name:border6_#{$animation-instance}; }

    }

    &.connect-at-hr-5, &.connect-at-hr-6 {

      .a1 > * { animation-name:border6_#{$animation-instance}; }

      .a3 > * { animation-name:border1_#{$animation-instance}; }

      .a5 > * { animation-name:border2_#{$animation-instance}; }

      .a7 > * { animation-name:border3_#{$animation-instance}; }

      .a9 > * { animation-name:border4_#{$animation-instance}; }

      .a11 > * { animation-name:border5_#{$animation-instance}; }

    }

    &.connect-at-hr-7, &.connect-at-hr-8 {

      .a1 > * { animation-name:border5_#{$animation-instance}; }

      .a3 > * { animation-name:border6_#{$animation-instance}; }

      .a5 > * { animation-name:border1_#{$animation-instance}; }

      .a7 > * { animation-name:border2_#{$animation-instance}; }

      .a9 > * { animation-name:border3_#{$animation-instance}; }

      .a11 > * { animation-name:border4_#{$animation-instance}; }

    }

    &, &.connect-at-hr-9, &.connect-at-hr-10 { /* Official CHR brand animation point position */

      .a1 > * { animation-name:border4_#{$animation-instance}; }

      .a3 > * { animation-name:border5_#{$animation-instance}; }

      .a5 > * { animation-name:border6_#{$animation-instance}; }

      .a7 > * { animation-name:border1_#{$animation-instance}; }

      .a9 > * { animation-name:border2_#{$animation-instance}; }

      .a11 > * { animation-name:border3_#{$animation-instance}; }

    }

    &.connect-at-hr-11, &.connect-at-hr-12 {

      .a1 > * { animation-name:border3_#{$animation-instance}; }

      .a3 > * { animation-name:border4_#{$animation-instance}; }

      .a5 > * { animation-name:border5_#{$animation-instance}; }

      .a7 > * { animation-name:border6_#{$animation-instance}; }

      .a9 > * { animation-name:border1_#{$animation-instance}; }

      .a11 > * { animation-name:border2_#{$animation-instance}; }

    }

  }

  @keyframes fade_#{$animation-instance} {
    0% {
      opacity:0;
    }
    0.5% {
      opacity:0;
    }
    3% {
      opacity:1;
    }
    90% {
      opacity:1;
    }
    99.5% {
      opacity:0;
    }
    100% {
      opacity:0;
    }
  }

  @keyframes opacify-sub_#{$animation-instance} {
    0% {
      opacity:0;
    }
    38% {
      opacity:0;
    }
    50% {
      opacity:1;
    }
    100% {
      opacity:1;
    }
  }

  @keyframes grow-long-arrows_#{$animation-instance} {
    0% {
      height:0;
    }
    8% {
      height:0;
    }
    14% {
      height:100%;
    }
    100% {
      height:100%;
    }
  }

  @keyframes grow-long-arrow-left-wing_#{$animation-instance} {
    0% {
      top:97.4%;
      right:21%;
      width:58%;
    }
    8% {
      top:97.4%;
    }
    14% {
      top:-2.7%;
      right:21%;
      width:58%;
    }
    17% {
      top:7.1%;
      right:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
    100% {
      top:7.1%;
      right:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
  }

  @keyframes grow-long-arrow-right-wing_#{$animation-instance} {
    0% {
      top:97.4%;
      left:21%;
      width:58%;
    }
    8% {
      top:97.4%;
    }
    14% {
      top:-2.7%;
      left:21%;
      width:58%;
    }
    17% {
      top:7.1%;
      left:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
    100% {
      top:7.1%;
      left:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
  }

  @keyframes begin-short-arrows_#{$animation-instance} {
    0% {
      opacity:0;
    }
    17% {
      opacity:0;
    }
    17.001% {
      opacity:1;
    }
    100% {
      opacity:1;
    }
  }

  @keyframes grow-short-arrows_#{$animation-instance} {
    0% {
      height:0%;
    }
    17% {
      height:0%;
    }
    17.001% {
      height:9%;
    }
    23% {
      height:100%;
    }
    100% {
      height:100%;
    }
  }

  @keyframes grow-short-arrow-left-wing_#{$animation-instance} {
    0% {
      opacity:0;
      top:87.6%;
      right:21%;
      width:58%;
    }
    17% {
      opacity:0;
      top:87.6%;
    }
    17.001% {
      opacity:1;
    }
    23% {
      top:-3.4%;
      right:21%;
      width:58%;
    }
    26% {
      top:9%;
      right:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
    100% {
      opacity:1;
      top:9%;
      right:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
  }

  @keyframes grow-short-arrow-right-wing_#{$animation-instance} {
    0% {
      opacity:0;
      top:87.6%;
      left:21%;
      width:58%;
    }
    17% {
      opacity:0;
      top:87.6%;
    }
    17.001% {
      opacity:1;
    }
    23% {
      top:-3.4%;
      left:21%;
      width:58%;
    }
    26% {
      top:9%;
      left:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
    100% {
      opacity:1;
      top:9%;
      left:0%;
      width:percentage($chrLogo-fixed-arrow-wing/$chrLogo-fixed-thickness);
    }
  }

  @keyframes border1_#{$animation-instance} {
    0% {
      right:100%;
      width:50%;
    }
    26% {
      right:100%;
    }
    28% {
      right:50%;
      width:50%;
    }
    30% {
      right:0%;
    }
    34% {
      right:-100%;
      width:100%;
    }
    34.0001% {
      right:100%;
    }
    36% {
      right:100%;
    }
    38% {
      right:0%;
    }
    100% {
      right:0%;
    }
  }

  @keyframes border2_#{$animation-instance} {
    0% {
      right:100%;
    }
    30% {
      right:100%;
    }
    32% {
      right:0%;
    }
    34% {
      right:0%;
    }
    38% {
      right:-100%;
    }
    38.0001% {
      right:100%;
    }
    50% {
      right:0%;
    }
    100% {
      right:0%;
    }
  }

  @keyframes border3_#{$animation-instance} {
    0% {
      right:100%;
    }
    32% {
      right:100%;
    }
    33% {
      right:0%;
    }
    100% {
      right:0%;
    }
  }

  @keyframes border4_#{$animation-instance} {
    0% {
      right:100%;
    }
    33% {
      right:100%;
    }
    34% {
      right:0%;
    }
    100% {
      right:0%;
    }
  }

  @keyframes border5_#{$animation-instance} {
    0% {
      right:100%;
    }
    34% {
      right:100%;
    }
    35% {
      right:0%;
    }
    100% {
      right:0%;
    }
  }

  @keyframes border6_#{$animation-instance} {
    0% {
      right:100%;
    }
    35% {
      right:100%;
    }
    36% {
      right:0%;
    }
    100% {
      right:0%;
    }
  }

}
