.chrIcon:not(.chrLogo):not(.chrGear) {
    cursor:pointer;
}

.iconExample {
    position:relative;
    float:left;
    width:50px;
    height:50px;
    overflow:hidden;
    transition: all 250ms $timing-function-base 500ms;

    @include chrLogo_icon(1000px, $white, 1, $white, 0);

    @include chrSpinner4D_icon(30px, $white, 1s, 0);

    @include chrGraph4D_icon(1000px, $white, 1, $white, 1, $white, 0, 3s, 0);

    @include chrGear4D_icon(1000px, $white, $white, 8s, 0);

    @include chrCalendar4D_icon(1000px, $white, 1, $black, 1, $white, 0, 4s, 0);

    @include chrCaret4D_icon(shown, 1000px, $white, $white, 0.25s, 0s);

    > .chrIcon {
        transform: scale(0.030);
        transition: all 250ms $timing-function-base 750ms;
    }

    &:hover, &:focus, &:active {
        width:500px;
        height:500px;

        @include chrSpinner4D_icon(300px, $white, 1s, 0);

        > .chrIcon {
            transform: scale(0.30);
        }

    }

}
