body {
  h1,h2,h3,h4,h5,h6 {
    font-weight: 400;
    margin-top: 0;
  }

  h1,h2,h3 {
    color: #333;
  }
  h4,h5,h6 {
    color: #666;
  }
  h1 {
    font-size: 24px;
  }
  h2 {
    font-size: 20px;
  }
  h3 {
    font-size: 18px;
  }
  h4 {
    font-size: 16px;
  }
  h5 {
    font-size: 14px;
    font-weight: 600;
  }
  h6 {
    font-size: 14px;
  }
}
