/* --- START Navisphere Forms --- */

.form-control {
	border-color:rgba($input-border-gray,1.0);
	border-radius:0;
	color:rgba($dark-gray,1.0);
}

.form-group {

	label, .help-text {
		display:block;
	}

}

textarea {
	max-width:100%;
}

label {

	.ns-icon.ns-info {

		&::before {
			font-size:120%;
			cursor:pointer;
		}

	}

	&.required {
		position:relative;
		padding-left:12px;

		&::before {
			@include pseudo($content:'*');
	    background: transparent;
	    color: $brand-danger;
	    font-size: 20px;
			line-height: 15px;
	    top: 5px;
	    left: 0;
	    border-radius: 50%;
		}

	}

	&.form-control {
		padding-right:0;
		border:0;
	}

	&.form-control:not(.required) {
		padding-left:0;
	}

	blockquote & {
		margin-bottom:0;
	}

}

input[type=checkbox], 
input[type=radio] {
	position:relative;

	&::before, &::after {
		transition: all $timing-delay-base ease-out;
  }

	&::before {
		@include pseudo;
  	color:rgba($brand-primary,0.0);
  	left:-1px;
  	top:-2px;
  	width:16px;
  	height:16px;
  	border:1px solid rgba($input-border-gray,1.0);
		cursor:pointer;
  }

  &:not(:disabled) {

	  &::before {
  		background:rgba($white,1.0);
	  }

	  &:checked::before {
  		color:rgba($brand-primary,1.0);
  		border-color:rgba($brand-primary,1.0);
	  }

	}

	&:disabled {
		
		&::before {
  		background:rgba($disabled-gray,1.0);
			cursor:not-allowed;
	  }

	}

}

input[type=checkbox] {

  &::before {
	font-size: 14px;
	font-weight: bolder;
    border-radius:3px;
  }

	&:indeterminate::before {
		content: '\2012';
		line-height: 85%;
		padding-left: 3px;
		color: rgba($brand-primary,1.0);
		border-color: rgba($brand-primary,1.0);
	}

  &:checked::before {
    content:'\2714';
    line-height: 1;
    padding-left: 0.5px;
  }

  &:disabled {

    &:checked::before,
		&:indeterminate::before {
      color:rgba($placeholder-gray,1.0);
    }

  }

}

input[type=radio] {

	&::before, &::after {
  	border-radius:50%;
  }

  &::after {
		@include pseudo;
  	background:rgba($brand-primary,0.0);
  	left:2px;
  	top:1px;
  	width:10px;
  	height:10px;
		cursor:pointer;
  }

	&:not(:disabled) {

	  &:checked::after {
	  	background:rgba($brand-primary,1.0);
	  }

	}

  &:disabled {

    &:checked::after {
      background:rgba($placeholder-gray,1.0);
    }

  }

}

.radio, .checkbox {
	margin-bottom:$spacing-base*2;

	&:nth-child(1) {
		margin-top:0;
	}

	&.disabled {
		color:rgba($placeholder-gray,1.0);
		cursor:not-allowed;
	}

}

.form-inline .radio, 
.form-inline .checkbox,
.radio-inline,
.checkbox-inline {
	margin-right:$spacing-base;
}

.input-group {

  .input-group-addon {
    border-radius: 0;
    border-color: darkgray;
    background: $navisphere-ultra-light-gray;

    .ns-icon {

      &:before {
        margin: 0;
      }

    }
    
  }

}

@media screen and (min-width:$screen-sm-min) {

	.form-inline {

		.radio, .checkbox {
			padding-left:$spacing-base*2;
		}

		.radio input[type="radio"], .checkbox input[type="checkbox"] {
			position:absolute;
			margin-left:-$spacing-base*2;
		}

	}

}

/* --- END Navisphere Forms --- */