$sides: (
	top,
	right,
	bottom,
	left
);

// INNER SPACE
@each $side in $sides {
	$className: space-inner-#{$side};
	$style: padding-#{$side};

	@include space($className, $style);
}

// OUTER SPACE
@each $side in $sides {
	$className: space-outer-#{$side};
	$style: margin-#{$side};

	@include space($className, $style);
}

// NO SPACE
.space-no-outer{
	margin: 0 !important;
}

.space-no-inner{
	padding: 0 !important;
}

.space-none{
	margin: 0 !important;
	padding: 0 !important;
}
