h1,h2,h3,h4,h5,h6 {

  &.spacing {
    margin-top:40px;
  }

}

.truncate {
  @include truncate;
}

.fillPG {
  position:fixed;
  width:100%;
  height:100%;
  top:0;
  right:0;
  bottom:0;
  left:0;
  overflow:hidden;
}

.fillWidth {
  width:100%;
  height:auto;
}

.fillHeight {
  height:100%;
  width:auto;
}

.contain {
  max-width:100%;
  max-height:100%;
}

.darkBG {
  background:rgba($dark-gray,1.0);
}

.x {display:none !important;}

.camouflage {opacity:0 !important; visibility:hidden !important;}

.opacityTransition {transition: opacity 150ms $timing-function-base 150ms;}

.text-muted {
  color: #666 !important;
}

.positive-number {
  color:rgba($vibrant-green,1.0);
}

.negative-number {
  color:rgba($error-red,1.0);
}