// chrGraph - mixins - STATIC & ANIMATED //

/* key variables */
$chrGraph-fixed-width:                30px;

// mixin - static //

%chrGraph {

  > .chrIcon.chrGraph {
    cursor:crosshair;

    .a1 {

      ~ [class^="a"] {
        display: none;
      }

      > * {

        &::before, &::after {
          @include pseudo;
        }

      }

      .b1 {
        overflow:hidden;

        &, &::before, &::after {

        }

        &::after {
          left:0;
          bottom:0;
          width:100%;
        }

      }

      .b2 {

        &, &::before, &::after {

        }

      }

      .b3 {
        display:none;
      }

    }

  }

}

@mixin chrGraph_icon($width: 30px, $color: $active-blue, $color-opacity: 1, $glow-color: $white, $glow-color-opacity: 1, $bg-color: $white, $bg-color-opacity: 0) {

  $chrGraph-flex:                       $width/$chrGraph-fixed-width;
  $inner-height:               $width - (5px*$chrGraph-flex);
  $inner-width:                $width - (3px*$chrGraph-flex);

  @extend %chrGraph;

  > .chrIcon.chrGraph {
    @include center($width, $width);
    background: rgba($bg-color, $bg-color-opacity);

    .a1 {
      @include center($width, $width);

      > * {

        &::before, &::after {
          background: rgba($color, $color-opacity);
        }

      }

      .b1 {
        @include center($inner-width, ($inner-height - 1*$chrGraph-flex));
        margin-left: -12px*$chrGraph-flex;

        &, &::before, &::after {

        }

        &::before {
          background:rgba($color, $color-opacity*0.4);
          left:4px*$chrGraph-flex;
          bottom:-11px*$chrGraph-flex;
          width:4px*$chrGraph-flex;
          height:20px*$chrGraph-flex;
          border-radius:0.5px*$chrGraph-flex 0.5px*$chrGraph-flex 0 0;
          box-shadow:
            6px*$chrGraph-flex -4px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.6),
            12px*$chrGraph-flex -2px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.8),
            18px*$chrGraph-flex -6px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*1.0),
            24px*$chrGraph-flex -4px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*1.0),
            30px*$chrGraph-flex -8px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.8),
            36px*$chrGraph-flex -6px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.6),
            42px*$chrGraph-flex -10px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.4),
            48px*$chrGraph-flex 0px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.4),
            54px*$chrGraph-flex -4px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.6),
            60px*$chrGraph-flex -2px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*0.8),
            66px*$chrGraph-flex -6px*$chrGraph-flex 0px 0px rgba($color, $color-opacity*1.0)
        ;
        }

        &::after {
          height:1px*$chrGraph-flex;
          box-shadow:-2px*$chrGraph-flex 0px*$chrGraph-flex 2px*$chrGraph-flex 1px*$chrGraph-flex rgba($glow-color,$glow-color-opacity);
        }

      }

      .b2 {
        @include center($inner-width, $inner-height);
        border-left:solid 2px*$chrGraph-flex $color;
        border-bottom:solid 2px*$chrGraph-flex $color;
        margin-left: -12px*$chrGraph-flex;

        &, &::before, &::after {

        }

        &::before {
          top:2px*$chrGraph-flex;
          left:-4.5px*$chrGraph-flex;
          width:3px*$chrGraph-flex;
          height:1px*$chrGraph-flex;
          box-shadow:0px 10px*$chrGraph-flex 0px 0px rgba($color, $color-opacity), 0px 20px*$chrGraph-flex 0px 0px rgba($color, $color-opacity);
        }

        &::after {
          top:7px*$chrGraph-flex;
          left:-3.5px*$chrGraph-flex;
          width:2px*$chrGraph-flex;
          height:1px*$chrGraph-flex;
          box-shadow:0px 10px*$chrGraph-flex 0px 0px rgba($color, $color-opacity);
        }

      }

    }

  }

}


// mixin - animated //

@mixin chrGraph4D_icon($width: 30px, $color: $active-blue, $color-opacity: 1, $glow-color: $white, $glow-color-opacity: 1, $bg-color: $white, $bg-color-opacity: 0, $active-interval: 3s, $animation-instance: 1) {

  @include chrGraph_icon($width, $color, $color-opacity, $glow-color, $glow-color-opacity, $bg-color, $bg-color-opacity);

  $chrGraph-flex:                       $width/$chrGraph-fixed-width;

  &:hover, &:focus {

    > .chrIcon.chrGraph {

      .a1 {

        .b1 {

          &::before {
            animation: onUI_graph4D_#{$animation-instance} $active-interval linear infinite $timing-delay-base;
          }

          @keyframes onUI_graph4D_#{$animation-instance} {
            0%   {transform:translate(0px,0);}
            100% {transform:translate(-48px*$chrGraph-flex,0);}
          }

          &::after {
            box-shadow:0px*$chrGraph-flex 0px*$chrGraph-flex 2px*$chrGraph-flex 1px*$chrGraph-flex rgba($glow-color,$glow-color-opacity);
            transition: box-shadow 0ms $timing-function-base $timing-delay-base;
          }

        }

      }

    }

  }

}
