.noCurve {

  &, :not(.keyCurve) {

    &, &::before, &::after {

      border-radius:0 !important;

    }

  }

}

/*
   * Main Use-Case:
   * Applying "zero-gradient", "zero-shadow" & "zero-animation" adjusted UI when Citrix is detected.
   * Either apply manually or
   * allow a script to dynamically detect Citrix
   * then apply the "citrix" class (or the "no3D" & "no4D" classes) to the <html></html> element.
*/

.no3D, .citrix:not(.no3D) {

  &, :not(.keyImage) {

    &, &::before, &::after {
      background-image: none !important;
    }

  }

  &, :not(.key3D) {

    &, &::before, &::after {
      box-shadow: none !important;
      text-shadow: none !important;
    }

  }

}

.no4D, .citrix:not(.no4D) {

  &, :not(.key4D) {

    &, &::before, &::after {
      transition-duration: 0s !important;
      animation-duration: 0s !important;
    }

  }

}

.chrIcon, .a1, .a2, .a3, .b1, .b2, .b3, .atmosphere {
  @extend .keyCurve;
  @extend .key3D;
}

.chrSpinner, .ns-loading, .ns-spin, .ns-pulse {
  @extend .key4D;
}

.no4D {

  .chrIcon.chrGear .b1 ~ * {
    display:none !important;
  }

}