.btn {
  border-radius:$border-radius-base*0.5;
  border-top-width:2px;
  border-bottom-width:2px;

  &:not(:disabled):not(.disabled) {

    &:hover, &:focus, &.focus, &:active, &.active, &.shown {
      text-shadow:$glow-small-text;
      transition:
        text-shadow 150ms $timing-function-base,
        background 150ms $timing-function-base,
        color 150ms $timing-function-base
    ;
    }

    &:hover, &:focus {
    }

    &:active {
    }

  }

  &:disabled, &.disabled {
    opacity:1;

    &, &:hover, &:focus, &.focus, &:active, &.active, &.shown {
      background:linear-gradient(to right, rgba(#f5f5f5,1.0) 0%,rgba($white,1.0) 50%,rgba(#f5f5f5,1.0) 100%),rgba(#f5f5f5,1.0);
      color:rgba(#cccccc,1.0);
      border-color:#f1f1f1;
    }

  }

}

.btn-default {

  &, &:hover, &:focus, &.focus, &:active, &.active, &.shown {
    color:rgba($brand-primary,1.0);
  }

}

/* --- END chrBootstrap Buttons --- */