// chrMobileMenuIcon4D - mixins - STATIC & ANIMATED //

// mixin - animated //

%chrMobileMenuIcon4D {

  > .menuIcon {
    display:none;
    position:relative;
    height:50px;
    cursor:pointer;

    &::before, &::after {
      @include pseudo;
      top:23px;
      left:15px;
      width:30px;
      height:3px;
      border-radius:3px;
      transition: all 1.2s ease-in-out;
    }

    &:not(.collapsedMenu) {

      &::before {
        transform:rotate(45deg);
      }

      &::after {
        transform:rotate(-45deg);
      }

    }

    &.collapsedMenu {

      &::before {
        transform:rotate(180deg) translateY(-5px);
      }

      &::after {
        transform:rotate(180deg) translateY(5px);
      }

    }

    &.navicon {

      &::before, &::after {
        background:none;
        top:auto;
        left:15px;
        width:auto;
        height:auto;
      }

      &::before {
        font-family:'navicon';
        font-size:32px;
      }

      &:not(.collapsedMenu) {

        &::before {
          content:"\f004";
          transform:rotate(0deg);
        }

      }

      &.collapsedMenu {

        &::before {
          content:"\f014";
          transform:rotate(0deg);
        }

      }

    }

  }

}

@mixin chrMobileMenuIcon4D($background-image, $background-color, $color, $border-radius, $screen-threshold) {

  @extend %chrMobileMenuIcon4D;

  > .menuIcon {
    background-image:$background-image;
    background-color:$background-color;
    border-radius:$border-radius;

    &::before, &::after {
      background:$color;
    }

    &.navicon {

      &::before {
        color:$color;
      }

    }

  }

  @media screen and (max-width:$screen-threshold) {

    > .menuIcon {

      display:block;

      &.collapsedMenu {

        + .menuItems {
          display:none;
        }

      }

    }

  }

}