html.template_dashboardPresentation { 
  min-width:400px;

  /* Move down content because we have a fixed navbar that is 50px tall */
  body {
    
  }


  /*
   * Global add-ons
   */

  .sub-header {
    padding-bottom: $spacing-base;
    border-bottom: 1px solid #eee;
  }

  /*
   * Top navigation
   * Hide default border to remove 1px line.
   */
  .navbar-fixed-top {
    border: 0;
  }

  /* navbar */

  .navbar-fixed-top, .navbar-fixed-bottom {
    position:relative;
    z-index:0;
  }

  .navbar-inverse .navbar-collapse, .navbar-inverse .navbar-form {
    border-color:rgba($ultra-light-gray,1.0);
  }

  .navbar-nav.navbar-right {
    clear:both;
  }

  .navbar {
  }

  .navbar-inverse {
    background-color:rgba($white,1.0);
    border-bottom:solid 1px rgba($ultra-light-gray,1.0);

    .navbar-nav {

      > li {

        a {
          font-size:16px;
          line-height:40px;
          padding:6px $spacing-base 5px;
          margin:0 $spacing-base;
          border-radius:8px;

          &, &:hover, &:focus, &:active, &:visited {
            color:rgba($brand-primary,1.0);
          }

          &:hover, &:focus, &:active, &:visited {
            text-decoration:underline;
          }

        }

      }

    }

  }

  .navbar-brand {
    font-weight:bold;
    padding:0px 5px 7px;
    border-radius:8px;

    img {
      display:inline-block;
    }

    span {
      color:rgba($medium-gray,1.0);
      font-weight:bolder;
      line-height:48px;

      em {
        color:rgba($theme-color1,1.0);
      }

    }

  }

  /*
   * Sidebar
   */

  .sidebar {
    background:rgba($white,1.0);
    z-index: 1000;
    display: block;
    padding-top:0;
    padding-left:0;
    padding-right:0;
    overflow: auto;

    &#jsNavState {

      span.hdr {

        &:not(.shown) {
          
          &::before {
            content:"\f018";
          }

          + ul {
            display:none;
          }

        }

        &.shown {
          
          &::before {
            content:"\f019";
          }

          + ul {
            display:block;
          }

        }

      }

    }

  }

  /* Sidebar navigation */

  .nav-sidebar:not(:nth-child(1)):nth-last-child(1) {
    left:0;
    bottom:0;

    a {

      span {
        color:rgba($brand-primary,1.0);
      }

      &:hover, &:focus, &:active, &:visited {
        border-left-color:rgba($white,1.0) !important;

        span {
          text-decoration:underline;
        }

      }

    }

  }

  .nav-sidebar {

    span.hdr {

      &::before {
        @include pseudo($content:"\f018");
        right:$spacing-base;
        font-family:'navicon';
      }

      + ul {
        display:none;
      }

    }

    li {
      font-size:0; /* IE spacing fix */
    }

    > li {

      span.hdr, a {

        display:block;
        padding-top:0px;
        padding-right:35px;
        padding-bottom:0px;
        padding-left:$spacing-base;
        line-height:40px;
        @include truncate;
        border-left:solid 5px rgba($theme-color1,0.0);

        color:rgba($dark-gray,1.0);
        cursor:pointer;
        transition: 
          background 0.6s ease-out, 
          text-shadow 0.6s ease-out, 
          animation 0.6s ease-out,
          border-bottom-color 0.6s ease-out, 
          border-left-color 0.6s ease-out, 
          border-right-color 0.6s ease-out, 
          box-shadow 0.6s ease-out, 
          font-weight 100ms ease-out
        ;

        &:hover {
          border-left-color:rgba($theme-color1,1.0);
          transition:border-left-color 100ms ease-out;
        }

      }

      > span.hdr, > a {
        font-size:16px;
        padding-left:15px;
        border-bottom:solid 1px rgba($ultra-light-gray,1.0);

        &, &:hover, &:focus, &:active, &:visited {
          background-color:rgba($white,1.0);
        }

      }

      > ul {
        padding-left:0;

        > li {

          a {

            &, &:hover, &:focus, &:active, &:visited {
              text-decoration:none;
            }

          }

          > a {
            font-size:16px;
            padding-left:25px;

            &, &:hover, &:focus, &:active, &:visited {
              background-color:rgba($ultra-light-gray,1.0);
            }

          }

          > ul {

            > li {

              > a {
                padding-left:35px;

                &, &:hover, &:focus, &:active, &:visited {
                  background-color:rgba($light-gray,1.0);
                }

              }

            }

          }

        }

      }

    }

  }


  /*
   * Main content
   */

  .beside-blockquote {
    margin-top:14px;
  }

  .above-blockquote {
    margin-bottom:0;
  }

  .beside-p {
    margin-bottom:0;
  }

  h4.ns-icon.ns-success ~ p, h4.ns-icon.ns-error ~ p {
    padding-left:25px;
  }

  .main {
    padding: $spacing-base*2;
  }
  @media (min-width: 768px) {
    .main {
      padding-right: $spacing-base*4;
      padding-left: $spacing-base*4;
    }
  }
  .main .page-header {
    margin-top: 0;
  }

  body > .navbar > .container-fluid > .row, body > .container-fluid > .row {
    display:block;
  }

  .sidebar, .navbar-header {
    min-width:275px;
    width:auto;
  }

  [role="article"].main, #navbar {
    flex: 100%; 
  }

  body > .container-fluid > .row {
    position:relative;
    padding-top:1px;

    @include chrMobileMenuIcon4D(none, transparent, rgba($brand-primary,1.0), 0, $screen-xs-max);

    > .menuIcon {
      width:65px;
      margin-top:-51px;
    }

  }

  .navbar {
    margin-bottom:0px;
  }

  &:not(.no4D):not(.citrix) [role="article"].main {
    @extend %appear-from-front;
  }

  [role="article"].main {
    overflow: auto;
    padding-top: 0px;
    width: auto;
    margin-left: 0px;

    &:not(#content_Home) {
      display: none;
    }

    .jumbotron {
      position:relative;
      background:rgba($near-white,1.0);
      margin-left:-$spacing-base*2;
      margin-right:-$spacing-base*2;
      border-bottom:solid 5px rgba($theme-color1,1.0);
      border-radius:0;

      h2 {
        font-weight:bold;
      }

      ~ * {
        max-width:830px;
      }

    }

  }

  .panel-group {

    .panel-default {
      border-left:0;
      border-right:0;
    }

    .panel {
      border-radius:0;

      + .panel {
        margin-top:0;
        border-top:0;
      }

    }

    .panel-heading {
      background-color:rgba($white,1.0);
      padding:0;
      border:0;

      h4 {

        a {
          display:block;
          position:relative;
          padding:$spacing-base 15px;

          &::before {
            position:absolute;
            right:$spacing-base;
            font-family:'navicon';
          }

          &:not(.collapsed) {
            color:rgba($brand-primary,1.0);

            &::before {
              content:"\f019";
            }

          }

          &.collapsed::before {
            content:"\f018";
          }

          &, &:hover, &:focus, &:active, &:visited {
            text-decoration:none;
          }

          &:hover {
            color:rgba($brand-primary,1.0);
          }

        }

      }

    }

    .panel-heading + .panel-collapse > .panel-body, .panel-heading + .panel-collapse > .list-group {
      border-top:0;
    }

    .panel-body {
      padding-bottom:$spacing-base*4;
    }

  } 

}

@media screen and (max-width:$screen-xs-max) {

  html.template_dashboardPresentation {

    .navbar-brand {
      width:100%;
      padding-left:0 !important;
      padding-right:0 !important;

      img {
        display:block;
        margin:0 auto;
      }

    }

    .navbar-form, .navbar-nav {
      margin-top:0;
      margin-bottom:0;
    }

    .navbar-inverse .navbar-nav > li a {
      padding-left:5px;
      padding-right:5px;
    }

    .navbar-nav.navbar-right {
      display:flex;
      justify-content:flex-end;
    }

  }

}

@media screen and (min-width:$screen-sm-min) {

  html.template_dashboardPresentation {

    &, body, body > .container-fluid, body > .container-fluid > .row, [role="article"].main {
      height:100%;
    }

    body {
      padding-top: 51px;
      /*padding-top:101px; Future Release */
    }

    body > .navbar > .container-fluid > .row, body > .container-fluid > .row {
      display:flex;
    }

    .navbar-form.navbar-left {
      float:right !important;
    }

    .navbar-fixed-top, .navbar-fixed-bottom {
      position:fixed;
      z-index:1030;
    }

    .sidebar {
      height:100%;
      border-right:solid 1px rgba($ultra-light-gray,1.0);
    }

    .nav-sidebar:nth-last-child(2) {
      padding-bottom:41px;
    }

    .nav-sidebar:not(:nth-child(1)):nth-last-child(1) {
      position:fixed;
      width:257px;
    }

    [role="article"].main .jumbotron {
      padding-left:40px;
      margin-left:-40px;
      margin-right:-40px;
    }

  }

}

@media screen and (min-width:$screen-md-min) {

  html.template_dashboardPresentation {

    body {
      padding-top: 51px;
    }

    .navbar-form:nth-child(1) {
      padding-left:25px;
    }

    .navbar-nav.navbar-right {
      clear:none;
    }

  }

}

@media screen and (min-width:$screen-lg-min) {

  html.template_dashboardPresentation {

    .navbar-form.navbar-left {
      float:left !important;
    }

  }

}
