/* --- START Navisphere Buttons --- */

.btn {
	border-top-width: 1px;
	border-bottom-width: 3px;

	&:hover, &:focus {
		border-top-width: 1px;
		border-bottom-width: 3px;
	}

	&:active {
		border-top-width: 1px;
		border-bottom-width: 3px;
	}

	&:not(:disabled):not(.disabled) {

		&:hover, &:focus {
			border-top-width: 1px;
			border-bottom-width: 3px;
		}

		&:active {
			border-top-width: 1px;
			border-bottom-width: 3px;
		}

		&.btn-default {
			background-color: rgba($white, 1.0);
			border-color: transparent;

			&:focus,
			&.focus {
				background-color: rgba($white, 1.0);
				color: darken(rgba($brand-primary, 1.0), 10%);
				border-color: darken(rgba($brand-primary, 1.0), 25%);
			}

			&:hover {
				background-color: rgba($white, 1.0);
				color: darken(rgba($brand-primary, 1.0), 10%);
				border-color: darken(rgba($brand-primary, 1.0), 12%);
			}

			&:active,
			&.active,
			.open > &.dropdown-toggle {
				background-color: rgba($white, 1.0);
				color: darken(rgba($brand-primary, 1.0), 10%);
				border-color: darken(rgba($brand-primary, 1.0), 12%);

				&:hover,
				&:focus,
				&.focus {
					background-color: rgba($white, 1.0);
					color: darken(rgba($brand-primary, 1.0), 17%);
					border-color: darken(rgba($brand-primary, 1.0), 25%);
				}

			}

		}

	}

	.input-group-btn & {
		border-bottom-width: 1px;
		border-radius: 0;
		height: 34px;

		&:hover, &:focus {
			border-bottom-width: 1px;
		}

		&:active {
			border-bottom-width: 1px;
		}

		&:not(:disabled):not(.disabled) {

			&:hover, &:focus {
				border-bottom-width: 1px;
			}

			&:active {
				border-bottom-width: 1px;
			}

			&.btn-default {
				border-color: rgba($brand-primary, 1.0);

				&.btn:hover, &.btn:active {
					color: darken(rgba($brand-primary, 1.0), 10%);
					border-color: darken(rgba($brand-primary, 1.0), 12%);
				}

				&.btn:focus {
					color: darken(rgba($brand-primary, 1.0), 10%);
					border-color: darken(rgba($brand-primary, 1.0), 25%);
				}
			}

			&.btn-default.dropdown-toggle {
				border-color: darkgray;
				color: $navisphere-dark-gray;
				background-color: $white;

				.caret {
					color: $brand-primary;
				}

				&:hover, &:focus {
					color: $brand-primary;
				}

				&:active {
					color: $brand-primary;
				}

			}
		}

		&:disabled {
			border-color: darkgray;
		}

		&:first-of-type {
			margin-right: -2px;
			z-index: 3;
		}

		&.btn.ns-icon {

			&::before {
				margin: 0;
			}

		}

	}

}

/* --- END Navisphere Buttons --- */