// chrSpinner - mixins - ANIMATED //

/* key variables
Currently calculated manually.  Find a way to use Sass functions, including sqrt() & pow().  */
$chrSpinner-fixed-diameter:    100px;
$chrSpinner-fixed-long-side:   41.4213563px; // $chrSpinner-fixed-long-side: $chrSpinner-fixed-diameter/(1 + sqrt(2));

// mixin - animated //

%chrSpinner4D {

  > .chrIcon.chrSpinner {

    .a1 {

      ~ [class^="a"] {
        display:none;
      }

      > * {
        position: absolute;

        &::before, &::after {
          @include pseudo;
        }

      }

      .b1 {

        &::before {
          opacity: 0.9;
        }

        &::after {
          opacity: 0.5;
        }

      }

      .b2 {

        & {
          opacity: 0.8;
        }

        &::before {
          opacity: 0.7;
        }

        &::after {
          opacity: 0.4;
        }

      }

      .b3 {

        & {
          opacity: 0.6;
        }

        &::before {
          opacity: 0.3;
        }

        &::after {
          display:none;
        }

      }

    }

  }

}

@mixin chrSpinner4D_icon($diameter: 100px, $color: $white, $rotation-interval: 1.3s, $animation-instance: 1) {

  $chrSpinner-flex:              $diameter/$chrSpinner-fixed-diameter;
  $chrSpinner-flex-long-side:    $chrSpinner-fixed-long-side*$chrSpinner-flex;

  @extend %chrSpinner4D;

  > .chrIcon.chrSpinner {
    @include concentric($diameter);
    animation: rotate_spinner_#{$animation-instance} $rotation-interval linear infinite;

    .a1 {
      @include concentric($diameter);
      animation: view_spinner_#{$animation-instance} 2500ms ease-in-out;

      > * {

        &, &::before, &::after {
          @include concentric($spacing-base*$chrSpinner-flex);
          background: rgba($color, 1.0);
          box-shadow:0 0 0 ($spacing-base*1.5*$chrSpinner-flex) rgba($color,1.0);
        }

      }

      .b1, .b2 {

        &::before {
          transform: rotate(0deg) translate(-$chrSpinner-flex-long-side, 0);
        }

      }

      .b1 {

        & {
          transform: rotate(0deg) translate(($chrSpinner-flex-long-side*0.5), (-$diameter*0.5));
        }

        &::before {
          box-shadow:0 0 0 (($spacing-base*1.5 - 2)*$chrSpinner-flex) rgba($color,1.0);
        }

        &::after {
          transform: rotate(0deg) translate(0, $diameter);
          box-shadow:0 0 0 (($spacing-base*1.5 - 10)*$chrSpinner-flex) rgba($color,1.0);
        }

      }

      .b2 {

        & {
          transform: rotate(-90deg) translate(($chrSpinner-flex-long-side*0.5), (-$diameter*0.5));
          box-shadow:0 0 0 (($spacing-base*1.5 - 4)*$chrSpinner-flex) rgba($color,1.0);
        }

        &::before {
          box-shadow:0 0 0 (($spacing-base*1.5 - 6)*$chrSpinner-flex) rgba($color,1.0);
        }

        &::after {
          transform: rotate(0deg) translate(-$chrSpinner-flex-long-side, $diameter);
          box-shadow:0 0 0 (($spacing-base*1.5 - 12)*$chrSpinner-flex) rgba($color,1.0);
        }

      }

      .b3 {

        & {
          transform: rotate(-135deg) translate((-$chrSpinner-flex-long-side*0.5), (-$diameter*0.5));
          box-shadow:0 0 0 (($spacing-base*1.5 - 8)*$chrSpinner-flex) rgba($color,1.0);
        }

        &::before {
          transform: rotate(0deg) translate(0, $diameter);
          box-shadow:0 0 0 (($spacing-base*1.5 - 14)*$chrSpinner-flex) rgba($color,1.0);
        }

      }

    }

  }

  @keyframes rotate_spinner_#{$animation-instance} {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes view_spinner_#{$animation-instance} {
    0% {
      opacity: 0.0;
    }
    100% {
      opacity: 1.0;
    }
  }

}
