/* --- START chrBase Extends --- */

/* - Appear - */

%appear-from-top {
  animation: appear_from_top 1000ms ease-out;  
}

@keyframes appear_from_top {
  0% {opacity:0;transform:translateY(-10px);}
  25% {opacity:0;transform:translateY(-10px);}
  75% {opacity:1;transform:translateY(0px);}
  100% {opacity:1;transform:translateY(0px);}
}

%appear-from-bottom {
  animation: appear_from_bottom 1000ms ease-out;
}

@keyframes appear_from_bottom {
  0% {opacity:0;transform:translateY(10px);}
  25% {opacity:0;transform:translateY(10px);}
  75% {opacity:1;transform:translateY(0px);}
  100% {opacity:1;transform:translateY(0px);}
}

%appear-from-left {
  animation: appear_from_left 1200ms ease-out;
}

@keyframes appear_from_left {
  0% {opacity:0;transform:translateX(-50px);}
  15% {opacity:0;transform:translateX(-50px);}
  85% {opacity:1;transform:translateX(0px);}
  100% {opacity:1;transform:translateX(0px);}
}

%appear-from-right {
  animation: appear_from_right 1200ms ease-out;
}

@keyframes appear_from_right {
  0% {opacity:0;transform:translateX(50px);}
  15% {opacity:0;transform:translateX(50px);}
  85% {opacity:1;transform:translateX(0px);}
  100% {opacity:1;transform:translateX(0px);}
}

%appear-from-back {
	perspective:3800px;

	> * {
	  animation: appear_from_back 1200ms ease-out;
	}

}

@keyframes appear_from_back {
  0% {opacity:0;transform:translateZ(-4100px);}
  15% {opacity:0;transform:translateZ(-4100px);}
  85% {opacity:1;transform:translateZ(0px);}
  100% {opacity:1;transform:translateZ(0px);}
}

%appear-from-front {
	perspective:3800px;

	> * {
	  animation: appear_from_front 1200ms ease-out;
	}

}

@keyframes appear_from_front {
  0% {opacity:0;transform:translateZ(400px);}
  15% {opacity:0;transform:translateZ(400px);}
  85% {opacity:1;transform:translateZ(0px);}
  100% {opacity:1;transform:translateZ(0px);}
}

/* --- END chrBase Extends --- */