$base-font-navicons-partial-path: "navicons/web/Navicons-" !default; /* default directory for navicons with fonts directory */

@font-face {
  font-family: 'navicon';
  font-weight: normal;
  src:
    url('#{$base-font-partial-path}fonts/#{$base-font-navicons-partial-path}webfont.woff?v=1.0.0') format('woff');
}

.ns-icon {
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1;

  &::before {
    font-family: navicon;
    margin-right:$spacing-base*0.5;
  }

  &.ns-solo::before, .ns-solo &::before {
    margin-right:0;
  }

}

span, em, i, b, strong {

  &.ns-icon {
    display:inline-block;
  }

}

/* animation */

.ns-icon.ns-spin::before {
  display:inline-block;
  -webkit-animation: ns-spin 2s infinite linear;
  animation: ns-spin 2s infinite linear;
}
.ns-icon.ns-pulse::before, .ns-icon.ns-loading:not(.ns-spin):not(.ns-pulse)::before {
  display:inline-block;
  -webkit-animation: ns-spin 1s infinite steps(8);
  animation: ns-spin 1s infinite steps(8);
}
@keyframes ns-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

.ns-icon.ns-loading {
  -webkit-animation:ns-loading-delay 1s linear;
  -moz-animation:ns-loading-delay 1s linear;
  -o-animation:ns-loading-delay 1s linear;
}
@-webkit-keyframes ns-loading-delay {
  0% {opacity:0;}
  90% {opacity:0;}
  100% {opacity:1;}
}
@-moz-keyframes ns-loading-delay {
  0% {opacity:0;}
  90% {opacity:0;}
  100% {opacity:1;}
}
@-o-keyframes ns-loading-delay {
  0% {opacity:0;}
  90% {opacity:0;}
  100% {opacity:1;}
}

/* sizes */
.ns-icon { &.ns-1, &.ns-xxs           { font-size: 10px; } }
.ns-icon { &.ns-2, &.ns-xs            { font-size: 14px; } }
.ns-icon { &.ns-3, &.ns-sm            { font-size: 18px; } }
.ns-icon { &.ns-4, &.ns-md            { font-size: 22px; } }
.ns-icon { &.ns-5, &.ns-lg            { font-size: 26px; } }
.ns-icon { &.ns-6, &.ns-xl            { font-size: 30px; } }
.ns-icon { &.ns-7, &.ns-xxl           { font-size: 34px; } }

/* Icons */
