// chrCalendar - mixins - STATIC & ANIMATED //

/* key variables */
$chrCalendar-fixed-width:                30px;


// mixin - static //

%chrCalendar {

  > .chrIcon.chrCalendar {

    .a1 {

      ~ [class^="a"] {
        display: none;
      }

      &, > * {

        &::before, &::after {
          @include pseudo;
        }

      }

      &::before, &::after {
        z-index:10;
      }

      &::before {
        right:auto;
      }

      &::after {
        left:auto;
      }

      .b3 {
        display:none;
      }

    }

  }

}

@mixin chrCalendar_icon($width: 30px, $color: $white, $color-opacity: 1, $color-2: $black, $color-2-opacity: 1, $bg-color: $white, $bg-color-opacity: 0) {

  $chrCalendar-flex:                       $width/$chrCalendar-fixed-width;
  $chrCalendar-inner-width-2:              $width - (6px*$chrCalendar-flex);
  $chrCalendar-inner-width:                $width - (3px*$chrCalendar-flex);

  @extend %chrCalendar;

  > .chrIcon.chrCalendar {
    @include center($width, $width);
    background: rgba($bg-color, $bg-color-opacity);

    .a1 {
      @include center($chrCalendar-inner-width, $chrCalendar-inner-width);

      &, > * {

        &::before, &::after {
          background: rgba($color, $color-opacity);
        }

      }

      &::before, &::after {
        background:rgba($color,$color-opacity);
        top:-1.5px*$chrCalendar-flex;
        width:5px*$chrCalendar-flex;
        height:8px*$chrCalendar-flex;
        border:solid 1.5px*$chrCalendar-flex rgba($color-2,$color-2-opacity);
        border-radius:2px*$chrCalendar-flex;
      }

      &::before {
        left:5.5px*$chrCalendar-flex;
      }

      &::after {
        right:5.5px*$chrCalendar-flex;
      }

      > * {
        @include center($chrCalendar-inner-width-2, $chrCalendar-inner-width-2*2);
        margin-top:-$chrCalendar-inner-width-2*1.5;
        border-top:solid $chrCalendar-inner-width-2 transparent;

        &::before, &::after {

        }

        &::before {
          background:rgba($color-2,$color-2-opacity);
          @include center($chrCalendar-inner-width-2, $chrCalendar-inner-width-2);
          border-radius:2px*$chrCalendar-flex;
        }

        &::after {
          background:rgba($color,$color-opacity);
          top:7px*$chrCalendar-flex;
          left:1.5px*$chrCalendar-flex;
          width:4.5px*$chrCalendar-flex;
          height:4.5px*$chrCalendar-flex;
          box-shadow:
            5.5px*$chrCalendar-flex 0px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            11px*$chrCalendar-flex 0px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            16.5px*$chrCalendar-flex 0px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            0px*$chrCalendar-flex 5.5px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            5.5px*$chrCalendar-flex 5.5px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            11px*$chrCalendar-flex 5.5px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            16.5px*$chrCalendar-flex 5.5px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            0px*$chrCalendar-flex 11px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            5.5px*$chrCalendar-flex 11px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            11px*$chrCalendar-flex 11px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0),
            16.6px*$chrCalendar-flex 11px*$chrCalendar-flex 0px 0px rgba($color, $color-opacity*1.0)
        ;
        }

      }

      .b1 {

      }

      .b2 {

      }

    }

  }

}


// mixin - animated //

@mixin chrCalendar4D_icon($width: 30px, $color: $white, $color-opacity: 1, $color-2: $black, $color-2-opacity: 1, $bg-color: $white, $bg-color-opacity: 0, $active-interval: 4s, $animation-instance: 1) {

  @include chrCalendar_icon($width, $color, $color-opacity, $color-2, $color-2-opacity, $bg-color, $bg-color-opacity);

  $chrCalendar-flex:                       $width/$chrCalendar-fixed-width;

  &:hover, &:focus {

    > .chrIcon.chrCalendar {

      .a1 {
        perspective:$width*2.14;

        &::before, &::after {
          animation: onUI_bindingCovered4D_#{$animation-instance} $active-interval linear infinite $timing-delay-base;
        }

        @keyframes onUI_bindingCovered4D_#{$animation-instance} {
          0% {
            border-bottom-width:1.5px*$chrCalendar-flex;
          }
          15% {
            border-bottom-width:2px*$chrCalendar-flex;
          }
          25% {
            border-bottom-width:1.5px*$chrCalendar-flex;
          }
          100% {
            border-bottom-width:1.5px*$chrCalendar-flex;
          }
        }

        .b1 {
          animation: onUI_monthFlipIn4D_#{$animation-instance} $active-interval linear infinite $timing-delay-base;
        }

        @keyframes onUI_monthFlipIn4D_#{$animation-instance} {
          0% {
            transform: rotateX(-35deg);
          }
          15% {
            transform: rotateX(-35deg);
          }
          40% {
            transform: rotateX(0deg);
          }
          100% {
            transform: rotateX(0deg);
          }
        }

        .b2 {
          animation: onUI_monthFlipOut4D_#{$animation-instance} $active-interval linear infinite $timing-delay-base;
        }

        @keyframes onUI_monthFlipOut4D_#{$animation-instance} {
          0% {
            opacity: 1;
            transform: rotateX(0deg);
          }
          15% {
            opacity: 1;
          }
          25% {
            opacity: 0;
            transform: rotateX(35deg);
          }
          100% {
            opacity: 0;
            transform: rotateX(35deg);
          }
        }

      }

    }

  }

}
