// chrCaret - mixins - STATIC & ANIMATED //

/* key variables */
$chrCaret-fixed-width:       30px;
$chrCaret-fixed-short-side:  20px;
$chrCaret-alignment:         0.25;


// mixin - static //

%chrCaret {

  > .chrIcon.chrCaret {

    .a1 {

      ~ [class^="a"] {
        display: none;
      }

      .b1 {

        ~ [class^="b"] {
          display: none;
        }

        &::before, &::after {
          content:'';
        }

        &::before {
          transform: rotate(-45deg);
        }

        &::after {
          transform: rotate(45deg);
        }

      }

    }

  }

}

@mixin chrCaret_icon($active-class: shown, $width: 30px, $color: $white, $ui-color: $white) {

  $chrCaret-flex:              $width/$chrCaret-fixed-width;
  $chrCaret-flex-short-side:   $chrCaret-fixed-short-side*$chrCaret-flex;
  $chrCaret-flex-thickness:    $chrCaret-flex-short-side*0.25;

  @extend %chrCaret;

  > .chrIcon.chrCaret {
    @include center($width, $width);

    .a1 {
      @include center($width, $width);

      .b1 {
        @include center($width, $width);

        &::before, &::after {
          background: rgba($color, 1.0);
          @include center($chrCaret-flex-thickness, $chrCaret-flex-short-side);
          border-radius:$border-radius-base*0.125*$chrCaret-flex;
        }

      }

    }

    &.caretT, &.caretTtoB, &.caretTtoL, &.caretTtoR {

      .a1 .b1 {

        &::before {
          transform: rotate(-45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

        &::after {
          transform: rotate(45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

      }

    }

    &.caretB, &.caretBtoT, &.caretBtoL, &.caretBtoR {

      .a1 .b1 {

        &::before {
          transform: rotate(-45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

        &::after {
          transform: rotate(45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

      }

    }

    &.caretL, &.caretLtoR, &.caretLtoT, &.caretLtoB {

      .a1 .b1 {

        &::before {
          transform: rotate(-45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

        &::after {
          transform: rotate(45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

      }

    }

    &.caretR, &.caretRtoL, &.caretRtoT, &.caretRtoB {

      .a1 .b1 {

        &::before {
          transform: rotate(-45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

        &::after {
          transform: rotate(45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
        }

      }

    }

  }

  &.#{$active-class} {

    > .chrIcon.chrCaret {

      &.caretTtoB, &.caretLtoB, &.caretRtoB {

        .a1 .b1 {

          &::before {
            transform: rotate(-45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

          &::after {
            transform: rotate(45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

        }

      }

      &.caretBtoT, &.caretLtoT, &.caretRtoT {

        .a1 .b1 {

          &::before {
            transform: rotate(-45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

          &::after {
            transform: rotate(45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

        }

      }

      &.caretLtoR, &.caretTtoR, &.caretBtoR {

        .a1 .b1 {

          &::before {
            transform: rotate(-45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

          &::after {
            transform: rotate(45deg) translate((($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

        }

      }

      &.caretRtoL, &.caretTtoL, &.caretBtoL {

        .a1 .b1 {

          &::before {
            transform: rotate(-45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * $chrCaret-alignment) - ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

          &::after {
            transform: rotate(45deg) translate((($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)), (($chrCaret-flex-short-side * (-$chrCaret-alignment)) + ($chrCaret-flex-thickness * $chrCaret-alignment)));
          }

        }

      }

    }

  }

}


// mixin - animated //

@mixin chrCaret4D_icon($active-class: shown, $width: 30px, $color: $white, $ui-color: $white, $active-interval: 0.25s, $active-delay: 1s) {

  @include chrCaret_icon($active-class, $width, $color, $ui-color);

  $chrCaret-flex:              $width/$chrCaret-fixed-width;
  $chrCaret-flex-short-side:   $chrCaret-fixed-short-side*$chrCaret-flex;
  $chrCaret-flex-thickness:    $chrCaret-flex-short-side*0.25;

  &.#{$active-class} {

    > .chrIcon.chrCaret {

      .a1 .b1 {

        &::before, &::after {
          transition: all $active-interval $timing-function-base $active-delay;
        }

      }

    }

  }

}
