// chrGear - mixins - STATIC & ANIMATED //

/* key variables */
$chrGear-fixed-diameter:    30px;


// mixin - static //

%chrGear {

  > .chrIcon.chrGear {
    @include concentric(100%);

    .a1 {

      ~ [class^="a"] {
        display: none;
      }

      > * {
        opacity:0;
        background:transparent;

        &::before, &::after {
          content: '';
        }

      }

      .b1 {
        opacity:1;
      }

      .b2, .b3 {
        transform: rotate(-22.5deg);
      }

      .b3 {
        left:auto;
      }

    }

  }

}

@mixin chrGear_icon($diameter: 30px, $color: $medium-gray-2, $ui-color: $black) {

  $chrGear-flex:              $diameter/$chrGear-fixed-diameter;

  @extend %chrGear;

  > .chrIcon.chrGear {

    .a1 {
      @include concentric($diameter);

      > * {
        @include concentric($diameter*(1/3));
        box-shadow:0 0 0 ($diameter*(1/3)*(3/5)) rgba($color,1.0);

        &::before, &::after {
          background: rgba($color, 1.0);
          @include concentric($diameter*(1/3)*(3/5));
          border-radius:$border-radius-base*0.25*$chrGear-flex;
          box-shadow:0 (2*(($diameter*0.5) - (3*$chrGear-flex))) 0 0 rgba($color,1.0), (-(($diameter*0.5) - (3*$chrGear-flex))) (($diameter*0.5) - (3*$chrGear-flex)) 0 0 rgba($color,1.0), (($diameter*0.5) - (3*$chrGear-flex)) (($diameter*0.5) - (3*$chrGear-flex)) 0 0 rgba($color,1.0);
        }

        &, &::before, &::after {

        }

        &::before {
          transform: rotate(0deg) translate(0, ((-$diameter*0.5) + (3*$chrGear-flex)));
        }

        &::after {
          transform: rotate(45deg) translate(0, ((-$diameter*0.5) + (3*$chrGear-flex)));
        }

      }

      .b2 {
        left:(-$diameter*0.5) + (3*$chrGear-flex);
      }

      .b3 {
        left:auto;
        right:(-$diameter*0.5) - (2*$chrGear-flex);
      }

    }

  }

  &:hover, &:focus {

    > .chrIcon.chrGear {

      .a1 {

        > * {
          box-shadow: 0 0 0 ($diameter*(1/3)*(3/5)) rgba($ui-color, 1.0);

          &::before, &::after {
            background: rgba($ui-color, 1.0);
            box-shadow: 0 (2*(($diameter*0.5) - (3*$chrGear-flex))) 0 0 rgba($ui-color, 1.0), (-(($diameter*0.5) - (3*$chrGear-flex))) (($diameter*0.5) - (3*$chrGear-flex)) 0 0 rgba($ui-color, 1.0), (($diameter*0.5) - (3*$chrGear-flex)) (($diameter*0.5) - (3*$chrGear-flex)) 0 0 rgba($ui-color, 1.0);
          }

        }

      }

    }

  }

}


// mixin - animated //

@mixin chrGear4D_icon($diameter: 30px, $color: $medium-gray-2, $ui-color: $black, $ui-rotation-interval: 8s, $animation-instance: 1) {

  @include chrGear_icon($diameter, $color, $ui-color);

  $chrGear-flex:              $diameter/$chrGear-fixed-diameter;

  &:hover, &:focus {

    > .chrIcon.chrGear {

      .a1 {

        > * {
          transition: all 750ms $timing-function-base;

          &::before, &::after {
            transition: all 750ms $timing-function-base;
          }

        }

        .b1 {
          animation: rotate_main-gear_#{$animation-instance} $ui-rotation-interval linear infinite;
        }

        .b2, .b3 {
          opacity: 1.0;
          transition: all 250ms $timing-function-base;
          animation: rotate_side-gears_#{$animation-instance} $ui-rotation-interval linear infinite;
        }

      }

    }

  }

  @keyframes rotate_main-gear_#{$animation-instance} {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes rotate_side-gears_#{$animation-instance} {
    0% {
      transform: rotate(-22.5deg);
    }
    100% {
      transform: rotate(-382.5deg);
    }
  }

}
