@mixin theme-color1($theme-color1) {

  @include text-emphasis-variant('.text-theme-color1', $theme-color1);

  @include bg-variant('.bg-theme-color1', $theme-color1);

  $btn-theme-color1-color: $white;
  $btn-theme-color1-bg: $theme-color1;
  $btn-theme-color1-border: darken($btn-theme-color1-bg, 5%);
  .btn-theme-color1 {
    @include button-variant($btn-theme-color1-color, $btn-theme-color1-bg, $btn-theme-color1-border);
    // @include btn-styles($btn-theme-color1-bg);
  }

  $label-theme-color1-bg: $theme-color1;
  .label-theme-color1 {
    @include label-variant($label-theme-color1-bg);
  }

  $progress-bar-theme-color1-bg: $theme-color1;
  .progress-bar-theme-color1 {
    @include progress-bar-variant($progress-bar-theme-color1-bg);
    // @include progress-bar-styles($progress-bar-theme-color1-bg);
  }

  $panel-theme-color1-text: $white;
  $panel-theme-color1-border: $theme-color1;
  $panel-theme-color1-heading-bg: $theme-color1;
  .panel-theme-color1 {
    @include panel-variant($panel-theme-color1-border, $panel-theme-color1-text, $panel-theme-color1-heading-bg, $panel-theme-color1-border);
    // > .panel-heading   { @include panel-heading-styles($panel-theme-color1-heading-bg); }
  }

}