/* --- START chrBootstrap Cards --- */

.ns-card {
  padding: 20px;
  border-radius: 0;
  border: 1px solid $navisphere-light-gray;
  box-shadow: none;
  transition: 0.3s;
  background-color: $white;

  &.shadow {
    box-shadow: 2px 2px 6px 0 rgba(0,0,0,0.15);
  }

  &.hover {
    &:hover, &:focus, &:active {
      border: 1px solid $navisphere-medium-gray;
      box-shadow: 2px 2px 6px 2px rgba(0,0,0,0.15);
    }
  } /* END .hover */
} /* END .card */

/* --- END chrBootstrap Cards --- */